<template>
  <div class="flex-column">
    <label for="desc">问题描述</label>
    <textarea name="desc" id="desc" cols="30" rows="5"></textarea>
    <span class="pure-form-message">
      使用特殊标记 <code>##...##</code> 来插入空格和指定答案.
      <br>
      例子:
      <br>
      输入:
     "1 + 1 = ##2## ?"
      <br>
      输出:
      "1 + 1 = ___ ?"
    </span>
    <div v-if="editing" class="ml-auto">
      <button class="pure-button button-success" @click.prevent="toUpdate">
        更新
      </button>
      <button class="ml-1 pure-button button-error" @click.prevent="toCancel">
        取消
      </button>
    </div>
    <button v-else class="ml-auto pure-button pure-button-primary" @click.prevent="complete">+ 添加问题</button>
  </div>
</template>

<script>
import translate from '@/tools/translate';

export default {
  data() {
    return {
      editing: false,
    }
  },
  mounted() {
    this.$bus.$on('workshop-edit', this.toEdit);
  },
  destroyed() {
    this.$bus.$off('workshop-edit', this.toEdit);
  },
  methods: {
    toEdit(data) {
      this.editing = true;
      document.getElementById('desc').value = data.descRaw;
    },
    toCancel() {
      this.editing = false;
      document.getElementById('desc').value = '';
      this.$emit('cancel');
    },
    toUpdate() {
      const desc = document.getElementById('desc');
      if (desc.value.trim() === '') {
        this.$alert('error', '请填写问题描述');
        return;
      }
      const result = translate(desc.value);
      if (result.answer.length === 0) {
        this.$alert('error', '问题描述格式不正确, 请重新输入')
        return;
      }
      const question = {
        desc: result.desc,
        descRaw: desc.value,
        answer: result.answer,
      };
      this.$emit('update', question);
      this.editing = false;
      desc.value = '';
    },
    complete() {
      const desc = document.getElementById('desc');
      if (desc.value.trim() === '') {
        this.$alert('error', '请填写问题描述');
        return;
      }
      const result = translate(desc.value);
      if (result.answer.length === 0) {
        this.$alert('error', '问题描述格式不正确, 请重新输入')
        return;
      }
      const question = {
        desc: result.desc,
        descRaw: desc.value,
        answer: result.answer,
      };
      this.$emit('complete', question);
      desc.value = '';
    },
  },
};
</script>
