<template>
<div class="flex-row pd-1">
  <form class="w-50 flex-column pure-form pure-form-stacked">
    <div class="form-group">
      <span>访问限制</span>
      <label for="public" class="pure-radio">
        <input type="radio" id="public" name="accessbility" :value=0 v-model="settings.accessbility">
        公开
      </label>
      <label for="private" class="pure-radio">
        <input type="radio" id="private" name="accessbility" :value=1 v-model="settings.accessbility">
        私有
      </label>
      <span v-if="settings.accessbility === 0" class="pure-form-message">
        允许任何人访问
      </span>
      <span v-else class="pure-form-message">
        只允许指定用户访问
      </span>
    </div>
    <div class="form-group">
      <label  for="">时间限制</label>
      <input type="number" v-model="settings.timeLimit.minutes">
      <span class="pure-form-message">
        分
      </span>
      <input type="number" v-model="settings.timeLimit.seconds">
      <span class="pure-form-message">
        秒
      </span>
    </div>
    <div class="form-group">
      <span>计时方式</span>
        <label for="timerMode1">
          <input type="radio" name="timerMode" id="timerMode1" v-model="settings.timerMode" :value=0>
          单个问题  
        </label>
        <label for="timerMode2" class="ml-1">
          <input type="radio" name="timerMode" id="timerMode2" v-model="settings.timerMode" :value=1>
          所有问题
        </label>
        <span v-if="settings.timerMode === 0" class="pure-form-message">
          在限定时间内回答一个问题
        </span>
        <span v-else class="pure-form-message">
          在限定时间内回答所有问题
        </span>
    </div>
    <div class="form-group">
      <span>开放时间</span>
      <label for="schedule-on">
        <input type="radio" name="schedule" id="schedule-on" v-model="settings.schedule" :value=0>
        不限制
      </label>
      <label for="schedule-off" class="ml-1">
        <input type="radio" name="schedule" id="schedule-off" v-model="settings.schedule" :value=1>
        在特定时间开放
      </label>
      <span v-if="settings.openMode === 1" class="pure-form-message">
        测试可以在任何时间进行
      </span>
      <span v-else class="pure-form-message">
        测试只能在指定时间进行
      </span>
    </div>
    <div v-if="settings.schedule" class="form-group">
      <span class="form-group-label">Date & Time</span>
      <div class="pure-g">
        <input type="date"  id="scheduleDate" required v-model="settings.date">
        <input type="time"  id="scheduleTime" required v-model="settings.time">
      </div>
    </div>
    <div class="ml-auto mt-auto flex-row">
      <button class="ml-1 pure-button" @click.prevent="next(-1)">上一步</button>
      <button class="ml-1 pure-button" @click.prevent="reset">重置</button>
      <button class="ml-1 pure-button pure-button-primary" @click.prevent="save">
        保存 & 下一步
      </button>
    </div>
  </form>
    <whitelist 
    class="flex-1"
    v-if="settings.accessbility === 1"
    :list="settings.whitelist"
    @add-mail="toAddMail"
    @del-mail="toDelMail"
    /> 
</div>
</template>

<script>
import Whitelist from './component/whitelist.vue';

export default {
  components: {
    Whitelist,
  },
  data() {
    return {
      settings: {
        accessbility: 0,
        timeLimit: {
          minutes: 0,
          seconds: 10,
        },
        timerMode: 0,
        whitelist: [],
        schedule: 0,
        scheduleAt: 0,
        date: 0,
        time: 0,
      },
    };
  },
  mounted() {
    const settings = JSON.parse(localStorage.getItem('tests-settings'));
    if (settings) {
      this.settings = settings;
    }
  },
  methods: {
    toAddMail(mail) {
      if (this.settings.whitelist.indexOf(mail.toLowerCase()) === -1) {
        this.settings.whitelist.push(mail.toLowerCase());
      }
    },
    toDelMail(index) {
      this.settings.whitelist.splice(index, 1);
    },
    save() {
      if (this.settings.schedule) {
        const date = document.getElementById('scheduleDate');
        const time = document.getElementById('scheduleTime');
        if (date.checkValidity() && time.checkValidity()) {
          const dateTime = `${date.value} ${time.value}`;
          const timeStamp = new Date(dateTime).getTime();
          this.settings.scheduleAt = timeStamp;
        }
      }
      localStorage.setItem('tests-settings', JSON.stringify(this.settings));
      this.next(1);
    },
    next(offset) {
      this.$emit('next', offset);
    },
    reset() {
      const settings = JSON.parse(localStorage.getItem('tests-settings'));
      if (settings) {
        this.settings = settings;
      }
    },
  },
};
</script>