<template>
  <div class="flex-column">
    <label for="desc">问题描述</label>
    <textarea
      name="desc"
      id="desc"
      cols="60"
      rows="5"
      v-model="question.desc"
      required
    >
    </textarea>
    <label for="option">问题选项</label>
    <textarea
      name="option"
      id="option"
      cols="60"
      rows="5"
      v-model="option"
      required
    >
    </textarea>
    <span class="pure-form-message-inline">
      添加选项, 并选中正确的选项
    </span>
    <button class="pure-button" @click.prevent="addOption">
      + 添加选项
    </button>
    <ul>
      <li 
      v-for="(option, index) in question.options" :key="option"
      class="flex-row mt-1"
      >
        <radio
        name='options'
        :id="'option' + index"
        :message="option"
        :checked="question.answer === index"
        @change="toChange($event, index)"
        />
        <i class="fas fa-times flex-item-center ml-auto" @click="toDel(index)">
        </i>
      </li>
    </ul>
    <div v-if="editing" class="ml-auto">
      <button class="pure-button button-success" @click.prevent="toUpdate">
        更新
      </button>
      <button class="ml-1 pure-button button-error" @click.prevent="toCancel">
        取消
      </button>
    </div>
    <button v-else class="ml-auto pure-button pure-button-primary" @click.prevent="complete">+ 添加问题</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      question: {
        options: [],
        desc: '',
        answer: -1,
      },
      option: '',

      editing: false,
    };
  },
  mounted() {
    this.$bus.$on('workshop-edit', this.toEdit);
  },
  destroyed() {
    this.$bus.$off('workshop-edit', this.toEdit);
  },
  methods: {
    addOption() {
      const trimedOption = this.option.trim();
      if (trimedOption) {
        this.question.options.push(trimedOption);
        this.option = '';
      }
    },
    toChange(event, index) {
      if (event.target.checked) {
        this.question.answer = index;
      }
    },
    toDel(index) {
      this.question.options.splice(index, 1);
    },
    toEdit(data) {
      this.editing = true;
      this.question = data;
    },
    toCancel() {
      this.$emit('cancel');
      this.editing = false;
      this.reset();
    },
    toUpdate() {
      let ok = true;
      let msg = '';
      const trimedDesc = this.question.desc.trim();
      if (this.question.answer === -1) {
        ok = false;
        msg = '请选中正确答案';
      }
      if (this.question.options.length === 0) {
        ok = false;
        msg = '请添加选项';
      }
      if (!trimedDesc) {
        ok = false;
        msg = '请填写问题描述';
      }
      if (!ok) {
        this.$alert('error', msg);
        return;
      }
      this.$emit('update', this.deepCopy(this.question));
      this.editing = false;
      this.reset();
    },
    complete() {
      let ok = true;
      let msg = '';
      const trimedDesc = this.question.desc.trim();
      if (this.question.answer === -1) {
        ok = false;
        msg = '请选中正确答案';
      }
      if (this.question.options.length === 0) {
        ok = false;
        msg = '请添加选项';
      }
      if (!trimedDesc) {
        ok = false;
        msg = '请填写问题描述';
      }
      if (!ok) {
        this.$alert('error', msg);
        return;
      }
      this.$emit('complete', this.deepCopy(this.question));
      this.reset();
    },
    reset() {
      this.question = {
        options: [],
        desc: '',
        answer: -1,
      }
    }
  },
};
</script>