<template>
  <div class="flex-row">
    <sidebar 
    :tabs="tabs"
    :status="status"
    :active="active"
    @click="toClickTab"
    />
    <component
      class="flex-1"
      :is="tabs[active].component"
      @next='jumpTo'
    >
    </component>
  </div>
</template>

<script>
import Generals from './panels/generals.vue';
import Questions from './panels/questions.vue';
import Settings from './panels/Settings';
import Publish from './panels/publish.vue';

export default {
  components: {
    Generals,
    Questions,
    Settings,
    Publish,
  },
  data() {
    return {
      active: 0,
      tabs: [
        {
          name: '基础信息',
          component: 'generals',
        },
        {
          name: '问题',
          component: 'questions',
        },
        {
          name: '设置',
          component: 'settings',
        },
        {
          name: '发布',
          component: 'publish',
        }
      ],
      status: [
        false,
        false,
        false,
      ],
      counter: 0,
    };
  },
  methods: {
    toClickTab(index) {
      if (index === this.tabs.length - 1) {
        if (this.counter !== this.status.length) {
          this.$alert('error', '请先保存所有表单');
          return;
        }
      }
      this.active = index;
    },
    jumpTo(index) {
      if (index === 1) {
        if (!this.status[this.active]) {
          this.counter += 1;
        }
        this.$set(this.status, this.active, true);
      }
      if (this.active + index === this.tabs.length - 1) {
        if (this.counter !== this.status.length) {
          this.$alert('error', '请先保存所有表单');
          return;
        }
      }
      this.active += index;
    },
  },
};
</script>
