<template>
  <div class="flex-row pd-1">
    <form class="w-50 pure-form pure-form-stacked flex-column">
      <label for="type">问题类型</label>
      <select name="type" id="type" v-model="type" :disabled="editing">
        <option
        :value="type.component"
        v-for="type in types"
        :key="type.name"
        >
          {{ type.name }}
        </option>
      </select>
      <label for="score" >分数 </label>
      <input
        type="Number"
        min="0"
        id="score"
        name="score"
        placeholder="0"
        v-model="score"
      >
      <component
        :is="type"
        class="flex-1"
        @complete="toComplete"
        @update="toUpdate"
        @cancel="toCancel"
      />
      <div class="ml-auto mt-1 flex-row">
        <button class="pure-button" @click.prevent="next(-1)"> 上一步 </button>
        <button class="pure-button pure-button-primary ml-1" @click.prevent="next(1)"> 保存 & 下一步 </button>
      </div>
    </form>
    <div class="ml-1 pd-1 bd-1 shadow-1 overflow-y flex-1 relative" style="height: 80vh;">
      <p class="text-light">问题</p>
      <ul>
        <li class="mb-1" v-for="(question, index) in questions" :key="question.desc">
          <question-item
            :index="index + 1"
            :data="question"
            @del="del($event, index)"
            @edit="edit($event, index)"
          >
          </question-item>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import QuestionItem from '../components/question-item.vue';
import SingleChoice from '../components/single-choice.vue';
import MultipleChoice from '../components/multiple-choice.vue';
import BlankFilling from '../components/blank-filling.vue';

export default {
  components: {
    QuestionItem,
    SingleChoice,
    MultipleChoice,
    BlankFilling,
  },
  data() {
    return {
      types: [
        {
          name: '单选题',
          component: 'single-choice',
        },
        {
          name: '多选题',
          component: 'multiple-choice',
        },
        {
          name: '填空题',
          component: 'blank-filling',
        },
      ],

      editing: false,
      editingIndex: 0,

      questions: [],
      score: '',
      type: 'single-choice',
    };
  },
  mounted() {
    const questions = localStorage.getItem('tests-questions');
    if (questions) {
      this.questions = JSON.parse(questions);
    }
  },
  methods: {
    toComplete(data) {
      Object.assign(data, {
        type: this.type,
        score: this.score,
      });
      this.questions.push(data);
    },
    next(offset) {
      localStorage.setItem('tests-questions', JSON.stringify(this.questions));
      this.$emit('next', offset);
    },
    toCancel() {
      this.editing = false;
    },
    toUpdate(data) {
      this.editing = false;
      Object.assign(data, {
        type: this.type,
        score: this.score,
      })
      this.$set(this.questions, this.editingIndex, data);
    },
    del(data) {
      if (data && data.desc) {
        this.questions = this.questions.filter(q => q.desc !== data.desc);
        localStorage.setItem('tests-questions', JSON.stringify(this.questions));
      }
    },
    edit(data, index) {
      this.editing = true;
      this.editingIndex = index;
      this.type = data.type;
      this.$bus.$emit('workshop-edit', data);
    },
  },
};
</script>