export default function (text) {
  const reg = /##(\w+|\W+)##/g;
  const copy = text;
  const blank = '___';
  const result = {
    answer: [],
  };
  let match;
  do {
    match = reg.exec(copy);
    if (match) {
      result.answer.push(match[1]);
    }
  } while (match);
  result.desc = copy.replace(reg, blank);
  return result;
}
