<template>
  <div class="flex-column justify-center">
    <div v-if="success" class="flex-item-center mt-auto mb-auto">
      <p>发布成功</p>
      <p><router-link to="/">前往查看</router-link></p>
    </div>
    <div v-else class="flex-item-center mt-auto mb-auto">
      <button
        class="pure-button"
        @click="next(-1)"
        :disabled="flag"
        >上一步
      </button>
      <button 
        class="ml-1 pure-button pure-button-primary " 
        @click="togglePreview" 
        :disabled="flag"
        >
        预览
      </button>
      <button class="ml-1 pure-button button-success" @click="publish" :disabled="flag">发布</button>
    </div>
    <preview v-if="preview" :test="paper" @close="preview = false"/>
  </div>
</template>

<script>
import axios from 'axios';
import Preview from '../components/preview.vue';

export default {
  components: {
    Preview,
  },
  data() {
    return {
      paper: {},
      preview: false,
      success: false,
      flag: false,
    };
  },
  mounted() {
    const generals = JSON.parse(localStorage.getItem('tests-generals'));
    const questions = JSON.parse(localStorage.getItem('tests-questions'));
    const settings = JSON.parse(localStorage.getItem('tests-settings'));
    const user = JSON.parse(localStorage.getItem('user'));
    this.paper = {
      title: generals.title,
      intro: generals.intro,
      tags: generals.tags,
      category: generals.category,
      author: user.username,
      createdAt: Date.now(),
      // eslint-disable-next-line
      totalScore: questions.reduce((acc, cur) => acc += (+cur.score), 0),
      questions,
      settings,
    };
  },
  methods: {
    togglePreview() {
      this.preview = !this.preview;
    },
    next(offset) {
      this.$emit('next', offset);
    },
    clean() {
      localStorage.removeItem('tests-generals');
      localStorage.removeItem('tests-questions');
      localStorage.removeItem('tests-settings');
    },
    publish() {
      if (this.flag) {
        return;
      }
      this.flag = true;
      axios.post(this.$url.tests, this.paper)
        .then(() => {
          this.success = true;
          this.flag = false;
          this.clean();
        })
        .catch(() => {
          this.flag = false;
        });
    },
  },
};
</script>