<template>
  <div class="question-container mt-1 flex-row">
    <div class="flex-1">
      <div class="desc">
        #{{ index }}. 
        <br>
        {{ data.desc }}
      </div>
      <span class="question-score" :title="'score'">
        {{ data.score || 0 }}分
      </span>
      <br>
      <span class="question-type" :title="data.type">{{ data.type }}</span>
      <div class="main">
        <ul v-if="data.type === QUESTION_TYPE.SINGLE_CHOICE">
          <li class="mt-1" v-for="(option, index) in data.options" :key="option">
            <radio
            :disable="true"
            :checked="data.answer === index"
            :message="option"
            />
          </li>
        </ul>
        <span
          v-else-if="data.type === QUESTION_TYPE.BLANK_FILLING"
          v-for="(text, index) in data.answer"
          :key="index"
          class="mt-1 blank-filling-answer"
        >
          {{ index + 1 }}. {{ text }}
        </span>
        <ul v-else-if="data.type === QUESTION_TYPE.MULTIPLE_CHOICE">
          <li class="mt-1" v-for="(option, index) in data.options" :key="option.desc">
            <checkbox
              :checked="data.answers[index] ? true : false"
              :disabled="true"
              :message="option"
            />
          </li>
        </ul>
      </div>
    </div>
    <div class="flex-row" style="align-self: start">
      <i class="question-control fas fa-pencil-alt" @click="edit"></i>
      <i class="question-control fas fa-times" @click="del" style="margin-left: 0.6em"></i>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    data: {
      type: Object,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
  },
  mounted() {
  },
  methods: {
    edit() {
      this.$emit('edit', this.deepCopy(this.data));
    },
    del() {
      this.$emit('del', this.deepCopy(this.data)); 
    },
  },
};
</script>

<style lang="scss" scoped>
@import '@/assets/css/colors.scss';

.answer {
  white-space: pre-line;
}
.option {
  padding: 0.3em 0;
}

.question-container {
  margin-left: 0.4em;
  height: 100%;
  padding: 0.6em;
  border: 1px dashed silver;
  background-color: #eee;
  color: #777;
  border-radius: 2px;
}
.question-type {
  font-family: monospace;
  background: orange;
  max-width: 12em;
  display: inline-block;
  text-overflow: ellipsis;
  text-align: center;
  vertical-align: middle;
  overflow: hidden;
  color: #777;
}

.question-control:hover {
  cursor: pointer;
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.45);
}

.blank-filling-answer {
  display: block;
}

</style>
